.ColorPicker {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
}

.button:hover {
    opacity: 1;
}

.button {
    width: 40px;
    height: 40px;
    margin: 5px;
    border-width: 14px;
    border-radius: 50%;
    border-style: 'solid';
    opacity: 0.85;
    cursor: pointer;
}